@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&display=swap');
.nav, .mobNav {font-family: 'Noto Sans KR', sans-serif;}
.homeWrap {font-family: 'Noto Sans KR', sans-serif;
    h2 { font-size:40px;  }
    .sub {padding: 28px 0 30px;}
    .article1 .text {
        font-family: 'Noto Sans KR', sans-serif;
        font-weight: bold; 
        p {font-size: 45px; padding-bottom: 10px;}
       
    }
    .article2 {
        padding: 120px 0 180px;
        .row {
            h3 {font-size: 20px;}
            .local {
                display:flex;
                padding:30px 0;
                li {
                    width:20%; padding:10px; position: relative; margin-right: 10px;
                    a { display:flex; align-items:center;
                        img { width:100%; border-radius: 50%;  }
                    }
                    .locDes {
                        position: absolute;
                        top:100%; width: 100%; 
                        text-align: center; font-weight: 600;
                        span {font-size: 15px; color: #777; font-weight: normal; }
                        p:nth-child(2) {font-size: 19px; padding: 3px 0; font-weight: bold;}
                        p:nth-child(3) {font-size: 15px; color: #777; font-weight: normal;}
                    }
                }
            }
            
        }
    }

    .article3 { background-color: #F6F7F9; padding: 80px 0;
        width: 100%; text-align: center;
        .row {
            .lifeStyle {
                
                ul {
                    display:flex;
                    li { 
                        flex:1; padding:20px 20px 20px 0;
                        &:last-child { padding-right:0px }
                        strong {display: block; font-size: 20px; color: #111; padding-top: 20px;}
                        
                    }
                }
            }
        }
    }

    .article4 { width:100%; text-align:center; padding:100px 0;
        .row { display:flex; justify-content:space-between; 
            div.rowchild { width:48.5%; 
                .servBox {
                    position: relative;width:100%; margin-right: 20px;
                    img {opacity: 0.8;}
                    .serText { background:#000; opacity: 0.85; color:#fff;
                       position: absolute; bottom: 0; width: 100%; padding:35px 0;
                       p {font-size: 19px; font-weight: bold; padding-bottom: 10px;}
                       span{ font-size: 15px; font-weight: lighter; }
                    }
                }
                .serv3 {margin-right: 0px;}
               
                &.right { width:49%;
                    display:flex; flex-direction:column;
                    justify-content:space-between;
                    .rightRow1 { display:flex; justify-content:space-between;
                        
                    }
                }
            }
        }
    }
}